/*******************************
           Checkbox
*******************************/
/*--------------
    Content
---------------*/
.ui.checkbox {
  display: flex;
  align-items: center;
  height: 24px;
  font-size: 1rem;
  min-width: 24px; }

/* HTML Checkbox */
.ui.checkbox input[type="checkbox"],
.ui.checkbox input[type="radio"] {
  width: 24px;
  height: 24px; }

/*--------------
      Box
---------------*/
.ui.checkbox .box,
.ui.checkbox label {
  display: block;
  height: 24px;
  font-weight: bold;
  font-size: 16px;
  color: #000;
  letter-spacing: 0.35px;
  padding-top: 5px;
  padding-left: 36px; }

.ui.checkbox:before,
.ui.checkbox label:before {
  top: 1px;
  width: 24px;
  height: 24px;
  background: #F5F3F2;
  border: 2px solid #C4C4C4; }

/*--------------
    Checkmark
---------------*/
.ui.checkbox .box:after,
.ui.checkbox label:after {
  top: 5px;
  width: 24px;
  height: 24px;
  font-size: 19px; }

/*--------------
      Label
---------------*/
/* Inside */
.ui.checkbox label,
.ui.checkbox + label {
  color: #000;
  -webkit-transition: color 0.1s ease;
  transition: color 0.1s ease; }

/* Outside */
.ui.checkbox + label {
  vertical-align: middle; }

/*******************************
          States
*******************************/
/*--------------
      Down
---------------*/
.ui.checkbox .box:active::before,
.ui.checkbox label:active::before {
  background: #F5F3F2;
  border-color: #C4C4C4; }

.ui.checkbox .box:active::after,
.ui.checkbox label:active::after {
  color: #000; }

.ui.checkbox input:active ~ label {
  color: #000; }

/*--------------
     Focus
---------------*/
.ui.checkbox input:focus ~ .box:before,
.ui.checkbox input:focus ~ label:before {
  background: #F5F3F2;
  border-color: #C4C4C4; }

.ui.checkbox input:focus ~ .box:after,
.ui.checkbox input:focus ~ label:after {
  color: #000; }

.ui.checkbox input:focus ~ label {
  color: #000; }

/*--------------
     Active
---------------*/
.ui.checkbox input:checked ~ .box:before,
.ui.checkbox input:checked ~ label:before {
  background: #9886FD;
  border-color: #9886FD; }

.ui.checkbox input:checked ~ :after,
.ui.checkbox input:checked ~ label:after {
  opacity: 1;
  color: #fff; }

/*--------------
      Hover
---------------*/
.ui.checkbox:hover, .ui.checkbox .box:hover::before,
.ui.checkbox label:hover::before {
  background: #F5F3F2;
  border-color: #9886FD; }

.ui.checkbox label:hover,
.ui.checkbox + label:hover {
  color: #000; }

/*--------------
    Focus Hover
---------------*/
.ui.checkbox input:focus ~ .box:hover::before,
.ui.checkbox input:focus ~ label:hover::before {
  background: #F5F3F2;
  border-color: #9886FD; }

.ui.checkbox input:focus:hover ~ .box:after,
.ui.checkbox input:focus:hover ~ label:after {
  color: #000; }

.ui.checkbox input:focus ~ label:hover {
  color: #000; }

/*--------------
  Active Hover
---------------*/
.ui.checkbox:hover input:checked ~ .box:before,
.ui.checkbox:hover input:checked ~ label:before {
  background: #9886FD;
  border-color: #9886FD; }

/*--------------
  Active Focus
---------------*/
.ui.checkbox input:checked:focus ~ .box:before,
.ui.checkbox input:checked:focus ~ label:before {
  background: #9886FD;
  border-color: #9886FD; }

.ui.checkbox input:checked:focus ~ :after,
.ui.checkbox input:checked:focus ~ label:after {
  opacity: 1;
  color: #fff; }

/*--------------
    Read-Only
---------------*/
.ui.read-only.checkbox,
.ui.read-only.checkbox label {
  cursor: default; }

/*--------------
     Disabled
---------------*/
.ui.disabled.checkbox .box:after,
.ui.disabled.checkbox label,
.ui.checkbox input[disabled] ~ .box:after,
.ui.checkbox input[disabled] ~ label {
  cursor: default !important;
  opacity: 0.5;
  color: #000; }

